import { useRef, useState } from "react"

let defaultQuestions = [
  'What have I done today?',
  'How do I feel?',
  'What else am I going to do?'
];

const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December'
]
const days = [
  'Monday',
  'Tuesday',
  'Wednesday',
  'Thursday',
  'Friday',
  'Saturday',
  'Sunday'
]
const printDate = (d=new Date ()) => {
  return  `${days [d.getDay ()]}, ${months [d.getMonth ()]} ${d.getDate ()}`
}
const getTime = (d=new Date ()) => {  
  return `${(d.getHours () % 12 || 12)}:${`0${d.getMinutes ()}`.slice(-2)} ${d.getHours () > 11 ? 'pm' : 'am'}`;
}

function Write ({questions, display, freeze, settings, createEntry}) {
  const {getSetting} = settings;
  const [start, setStart] = useState (new Date ());
  const [newQuestionDialog, setNewQuestionDialog] = useState (false);
  const addQuestion = async () => {
    let q = addQuestionRef.current.innerText;
    addQuestionRef.current.innerText = '...?';
    await questions.createQuestion (q);
    setNewQuestionDialog (false);
  }
  const qaRef = useRef ();
  const ffRef = useRef ();
  const addQuestionRef = useRef ();
  const complete = async () => {
    let unfreeze = freeze ();
    let body = {start, end: new Date (), entryType: getSetting ('freeform') ? 'freeform' : 'questions'};
    try {
      if (body.entryType === 'questions') {
        body.questions = questions;
        body.answers = Array.from (qaRef.current.querySelectorAll ('div')).map (el => el.innerText);
      } else {
        body.freeform = ffRef.current.innerText;
      }
      await createEntry (body);
      setStart (new Date ());
      if (body.entryType !== 'freeform') Array.from (qaRef.current.querySelectorAll ('div')).map (el => el.innerText = '...');
      if (body.entryType === 'freeform') ffRef.current.innerText = 'Write your entry here!';
      unfreeze ();
    } catch (e) {
      if (body.entryType !== 'freeform') Array.from (qaRef.current.querySelectorAll ('div')).map (el => el.innerText = '...');
      if (body.entryType === 'freeform') ffRef.current.innerText = 'Write your entry here!';
      unfreeze ();
    }
  }
  if (newQuestionDialog) return (
    <main className={display}>
      <h2>Current Questions</h2>
      <ul className="current-question-list">
        {
          [...defaultQuestions, ...questions.questions].map (q => <li key={`current-questions-${q}`}>{q}</li>)
        }
      </ul>
      <b>What question would you like to add?</b>
      <b ref={addQuestionRef} className="add-question-input" contentEditable>...?</b>
      <div>
        <button onClick={addQuestion}>Add Question</button>
        <button onClick={() => setNewQuestionDialog (false)}>Cancel</button>
      </div>
    </main>
  )
  return (
    <main className={display}>
      <h2>{printDate ()}</h2>
      <h3>{getTime (start)}</h3>
      {
        !getSetting ('freeform') &&
        <div ref={qaRef} className="questions">
          {
            defaultQuestions.map (question => (
              <>
                <b>{question}</b>
                <div contentEditable>...</div>
              </>
            ))
          }
          {
            questions.questions.map (question => (
              <>
                <b>{question}</b>
                <div contentEditable>...</div>
              </>
            ))
          }
          <span className="fake-button" onClick={() => setNewQuestionDialog (true)}>
            Add Question
          </span>
        </div>
      }
      {
        getSetting ('freeform') &&
        <div className="freeform" contentEditable ref={ffRef}>Write your entry here!</div>
      }
      <button onClick={complete}>Complete</button>
    </main>
  )
}

export default Write